.wp-block-b-blocks-feature-boxes .bBlocksFeatureBoxes {
	display: grid;

	* {
		box-sizing: border-box;
	}

	.bBlocksFeature {
		.featureDetails {
			padding: 0 20px;
		}

		.featureIcon {
			width: auto;
			max-width: 100%;
			height: auto;

			i {
				font-style: inherit;
			}

			img {
				max-width: 100%;
			}
		}

		.featureTitle {
			margin: 0;
		}

		.featureSeparator {
			display: inline-block;
		}

		.featureDescription {
			margin: 0;
		}
	}
}